img {
  width: 20em;
  border-radius: 10px;
  margin-bottom: 1em;
}

.Certificates {
  height: 100vh;
}

.certheading {
  font-family: "Raleway", sans-serif;
  font-size: 3rem;
  background: linear-gradient(to right, #a88beb 0%, #6e45e1 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  text-align: center;
  margin-top: 5vh;
  margin-left: auto;
  margin-right: auto;
}

.certheading {
  width: 17rem;
  margin-bottom: 1em;
}

@media (max-width: 768px) {
  .certheading {
    font-size: 2rem;
    width: 11rem;
  }
  img {
    width: 19em;
  }
  .Certificates {
    height: 210vh;
  }
}
