.aboutheading {
  font-family: "Raleway", sans-serif;
  font-size: 3rem;
  background: linear-gradient(to right, #a88beb 0%, #6e45e1 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  /* color: #dbd0fa; */
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  width: 9rem;
}
.About {
  background-color: black;
  overflow: hidden;
  height: 100vh;
  padding: 10px;
}

.AboutSection {
  border-radius: 10px;
  padding: 2rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  border: 3px solid #00adb5;
  margin-top: 25vh;
}

.aboutInfo {
  color: white;
  text-align: justify;
  padding-top: 2em;
  font-size: 20px;
}
.ProfilePic {
  width: 25em;
}
@media screen and (max-width: 1203px) {
  .ProfilePic {
    width: 12em;
  }
  .aboutInfo {
    font-size: 20px;
  }
}

@media (max-width: 768px) {
  .aboutheading {
    font-size: 2rem;
    width: 6rem;
  }
  .ProfilePic {
    width: 9em;
    margin-left: auto;
    margin-right: auto;
  }
  .AboutSection {
    padding: 1rem;
  }
  .aboutInfo {
    font-size: 14px;
  }
}
