.projectheading {
  font-family: "Raleway", sans-serif;
  font-size: 3rem;
  background: linear-gradient(to right, #a88beb 0%, #6e45e1 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  text-align: center;
  margin-top: 5vh;
}

.projectheading {
  width: 12rem;
  margin-bottom: 1em;
}
.Project {
  height: 190vh;
}
.ProjectSection {
  border-radius: 10px;
  margin-top: 1rem;
  padding: 2rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.projectInfo {
  color: white;
  text-align: justify;
  font-size: 18px;
}

figcaption {
  color: white;
}

.ProjectSection img {
  width: 28em;
  border-radius: 10px;
}
.projectrow {
  margin-bottom: 4em;
}
.projectCarousel {
  width: 120%;
  height: 120%;
  text-align: center;
  margin-left: -10%;
}
.projectname {
  color: #00adb5;
  font-size: 25px;
}
@media (max-width: 992px) {
  .projectInfo {
    font-size: 12px;
  }
  .ProjectSection img {
    width: 20em;
    border-radius: 10px;
  }
}

@media (max-width: 768px) {
  .ProjectSection img {
    width: 20em;
    border-radius: 10px;
  }
  .Project {
    height: 220vh;
  }
  .projectrow {
    margin-bottom: 3em;
  }
  .projectname {
    color: #00adb5;
    font-size: 15px;
  }
}

@media (max-width: 768px) {
  .projectheading {
    font-size: 2rem;
    width: 8rem;
    margin-bottom: 10px;
  }

  .ProjectSection {
    padding: 1rem;
  }
  .projectInfo {
    font-size: 12px;
  }
}
