.contactheading {
  font-family: "Raleway", sans-serif;
  font-size: 3rem;
  background: linear-gradient(to right, #a88beb 0%, #6e45e1 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: 11rem;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5vh;
  margin-bottom: 1em;
}

.resume {
  width: 10em;
}
.resumecard {
  background-color: black;
  width: 40vh;
  height: 30vh;
}
.contactsection {
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
}
.Contact {
  height: 100vh;
  padding-top: 5vh;
  background-image: url("../assets/shadow.png");
}
.smallform {
  display: none;
}
@media (max-width: 768px) {
  .contactheading {
    font-size: 2rem;
    width: 8rem;
  }
  .Contact {
    height: 120vh;
    padding-top: 5vh;
    background-image: none;
  }
  .contact-wrapper {
    display: none;
  }
  .smallform {
    display: block;
    background-color: #03a9f5;
    padding: 1em;
    border-radius: 10px;
    margin-top: 2em;
  }
  form {
    font-size: smaller;
  }
}

.btn {
  text-decoration: none;
  border: 1px solid rgb(146, 148, 248);
  position: relative;
  overflow: hidden;
  color: white;
}

.btn:hover {
  color: white;
  box-shadow: 1px 1px 25px 10px rgba(146, 148, 248, 0.4);
}

.btn:before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    120deg,
    transparent,
    rgba(146, 148, 248, 0.4),
    transparent
  );
  transition: all 650ms;
}

.btn:hover:before {
  left: 100%;
}
